@mixin container-position-center {
  display: flex;
  flex-direction: column;
  align-items: center;
}

@mixin row-direction-justify {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

@mixin center-content-inside {
  display: flex;
  align-items: center;
  justify-content: center;
}

@mixin font-general-settings {
  font-family: Orbitron, sans-serif;
  font-weight: 400;
}